
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { RPT204000211_RES, RPT204000211_REQ, ActivityLogRecordReport } from "@/shared/type/RPT202001SS14.type"
import '@vuepic/vue-datepicker/dist/main.css'
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
const excel = new ExportExcel()

export default defineComponent({
    name: "rpt202001ss14",
    components: { Pagination, VueDatePicker },

    data() {
        return {
            date: '' as unknown,
            header: [
                { columnName: 'No.' },
                { columnName: 'Reference No.' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'User Name' },
                { columnName: 'User Role' },
                { columnName: 'Phone Number' },
                { columnName: 'Email ID' },
                { columnName: 'Activity log type' },
                { columnName: 'Access Device' },
                { columnName: 'Channel' },
                { columnName: 'First Login' },
                { columnName: 'Last Login' },
            ],
            table: [] as ActivityLogRecordReport[],
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        }
    },

    computed: {
        formattedDate(): string {
            return this.date ? this.$datetime.formatVueDatePicker(this.date as Date) : ''
        },
    },

    mounted() {
        this.getIbActivityLogRecordForCustomerDetail(false);
    },

    methods: {
        async getIbActivityLogRecordForCustomerDetail(isSearch: boolean) {
            const req = {
                applicationDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.pageSize,
                pageNumber: isSearch ? 0 : this.currentPage
            } as RPT204000211_REQ

            if (isSearch) {
                this.currentPage = 1
                this.isResetPage = true;
            }

            const res = await this.$request.getRequest("RPT20400211", req, false) as RPT204000211_RES

            this.table = this.onMappingTableData(res.body.list)
            this.totalItems = res.body.totalCount
        },

        async exportToExcel() {
            const customHeader = [
                'Reference No',
                'Biz CID',
                'Biz Name',
                'User Name',
                'User Role',
                'Phone Number',
                'Email ID',
                'Activity log type',
                'Access Device',
                'Channel',
                'First Login',
                'Last Login'
            ]

            const req = {
                applicationDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20400211", req, false) as RPT204000211_RES;

            const tableForExport = this.onMappingTableData(res.body.list)

            const dataForExport: ExportSheet = {
                data: tableForExport,
                columnHeader: [customHeader],
                sheetName: 'Activity Log Record Report'
            }
            excel.exportSheet([dataForExport], 'Activity Log Record Report')
        },

        onMappingTableData(lists: ActivityLogRecordReport[]): ActivityLogRecordReport[] {
            return lists.map((item: ActivityLogRecordReport) => ({
                referenceNo: item.referenceNo,
                customerNo: item.customerNo,
                customerName: item.customerName,
                username: item.username,
                userRole: item.userRole,
                cellPhoneNo: this.$format.formatPhoneNumber(item.cellPhoneNo),
                email: item.email,
                activityLogType: item.activityLogType,
                accessDevice: item.accessDevice,
                channel: item.channel,
                firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                lastChangeDate: this.$datetime.formatStringToStringDate(item.lastChangeDate)
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getIbActivityLogRecordForCustomerDetail(false);
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleReset() {
            this.date = '' as unknown
            this.currentPage = 1
            this.isResetPage = true
            this.getIbActivityLogRecordForCustomerDetail(false);
        }
    }
})
